.level-bar {
    background: black;
    height: 55px;
    display: flex;
    flex-direction: row;
    gap: 25px;
    justify-content:flex-start;
    align-items: center; /* Centra verticalmente */
    font-size: 20px;
    font-family: 'cincel';
}

.blood-status {
    display: flex;
    flex-direction: column;
    margin: 10px;
}

.blood-count {
    color: red;
}

.blood-tag {
    color: white;
    text-align: left;
}

.level-status {
    display: flex;
    align-items: center; /* Asegura que el ProgressBar se centre verticalmente */
}

.level-tag {
    color: white;
}

.bracket {
    width: 15px;
}

.exp-status {
    text-align: center;
}

.exp-tag {
    color: #fccc00;
    font-size: 15px;
    font-weight: bold;
}

.help {
    width: 30px;
    height: 30px;
    background-color: red;
    margin-right: 10px;
}